.banner-image {
    height: 567px;
    width: 100%;
    background-size: cover;
    background-position: center top;
    background-repeat: no-repeat;
    position: absolute;  

  @media screen and (max-width: $mobile-break) {
    height: 469px;
  }


    &:before {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      width: 100%;

    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#000000+0,000000+100&0.69+0,0.31+37,0+100 */
    background: -moz-linear-gradient(top, rgba(0,0,0,0.69) 0%, rgba(0,0,0,0.31) 37%, rgba(0,0,0,0) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgba(0,0,0,0.69) 0%,rgba(0,0,0,0.31) 37%,rgba(0,0,0,0) 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, rgba(0,0,0,0.69) 0%,rgba(0,0,0,0.31) 37%,rgba(0,0,0,0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#b0000000', endColorstr='#00000000',GradientType=0 ); /* IE6-9 */
    

    background-color: rgba(0,0,0,.15);

    @media screen and (max-width: $mobile-break) {
      background:none;
      background-color: rgba(0,0,0,.3);
    }
  
      // background-color: rgba(35, 31, 32, 0.25);
      z-index: 1;
    }

}
.featured-carousel-index2 {
    
    .card {
        color: #fefefe;
        background-color: transparent;
        position: relative;
        width: 100%;
        height: 435px;
        z-index: 1;
        display: -webkit-box;
        display: -moz-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-transition-property: -webkit-transform;
        -moz-transition-property: -moz-transform;
        -o-transition-property: -o-transform;
        -ms-transition-property: -ms-transform;
        transition-property: transform;
        -webkit-box-sizing: content-box;
        -moz-box-sizing: content-box;
        box-sizing: content-box;

        @media screen and (max-width: $mobile-break) {
            height: 300px;
        }

        figure {
            position: absolute;
            top: 0;
            bottom: 0;
            width: 100%;
            z-index: 0;

            img {
                width: 100%;
                height: auto;
            }
        }


        .content {
            display:block;
            padding: 0;
            position: absolute;
            top:100px;
            left: 0;
            right: 0;
            z-index: 20;

            @media screen and (max-width: $mobile-break) {
                top:38px;
                margin: 0 15px;
            }
  
      .category {
          @media screen and (max-width: $mobile-break) {
              font-size:rem(8);
              padding:3px 8px ;
              margin-bottom: 14px;

          }
      }



            h2 {
                font-size: rem(48);
                max-width: 60%; /* ~470/1200 */
                margin-bottom: 25px;

                @media screen and (max-width: 1580px) {
                    max-width: 60%;
                }

                @media screen and (max-width: 1260px) {
                    max-width: 80%;
                }
        
                @media screen and (max-width: $sm-break){
                  font-size: rem(40);
                  height:105px;
                }
                @media screen and (max-width: $mobile-break){
                  max-width:none;
                  height:auto;
                  max-height: 43px;
                  font-size: rem(18);
                  line-height:1.2;
                  margin-bottom:14px;

                  // font-size: ((40px / $font-size-base) + $font-unit);
                  // height:105px;
                }

                @media screen and (max-width: $mobile-break) {
                  height:110px;
                }
            }

            time {
                display: inline-block;
                margin-left: 14px;
                color: #fefefe;
                font-size: rem(11);

                @media screen and (max-width: $mobile-break) {
                    display: block;

                    font-size: rem(8);
                    margin-left: 0;

                }

            }

            .author {
                background-color: #ffff00;
                color: #1a1818;
                @media screen and (max-width: $mobile-break) {
                    font-size: rem(8);
                    font-weight:900;
                }
            }

            p {
                font-size:  rem(16);
                font-weight: 900;
                margin: 0;
                max-width: 51%; /* ~619/1200 */

                @media screen and (max-width: 1260px) {
                    max-width: 80%;
                }
                @media screen and (max-width: $sm-break){
                  height:80px;
                }
                @media screen and (max-width: $mobile-break) {
                    max-width: 90%;
                    font-size:  rem(14);

                }
            }

            .share {
                position: absolute;
                bottom: 0;
                left: 54%; /* just right of paragraphs */

                @media screen and (max-width: 1260px) {
                  left: 83%;
                }

                @media screen and (max-width: $mobile-break) {
                  position: static;
                  margin-top: 1em;
                }
            }
        }
    }

    .container {
        float: left;
        padding: 0;
        width: 23%;

        @media screen and (max-width: $logo-break) {
            width: 35%;
        }

        @media screen and (max-width: $mobile-break) {
            float: none;
            width: 100%;
        }
    }

    .top-stories {
        margin: 0 11.165048544% 0 7.766990291%; /* (r): 46/412; (l): 32/412 */
        padding: 0;

        @media screen and (max-width: $mobile-break) {
            margin: 0 15px;
        }

        h2 {
            margin: 0;
            padding: 0;
            width: auto;
            font-size: 1.384615385em; /* 18/13 */

            @media screen and (max-width: $mobile-break) {
                font-size: 2em; /* 26/13 */
            }
        }

        > h2:first-child {
            margin-top: 1.3em;
            padding: 0;
            width: auto;
        }


        .card {
            article {
                position: relative;
                display: block;
                margin: 26px 0 0;
                // padding: 0 0 26px 94px;
                border-bottom: 2px solid #d1d2d4;
                min-height: 108px;
                overflow: hidden;

                // @media screen and (max-width: $mmd-break)
                // {
                //   width: 100%;
                //   padding-left: 94px;
                //   padding-right: 0;
                //   float: none;
                //   height: auto;
                //   border-bottom: 2px solid #d1d2d4;
                // }

                figure {
                    position: absolute;
                    top: 0;
                    left: 0;
                    margin: 0;
                    padding: 0;
                    width: 80px;
                    height: 80px;
                    overflow: hidden;
                }

                h3 {
                    margin: 0;
                    font-size: 1.307692308em; /* 17/13 */
                    font-weight: bold;
                    line-height: 1.411764706em; /* 24/17 */
                }

                time + h3 {
                    margin-top: 0.5em; /* ~8px */
                }

                h3 + time {
                    margin-top: 0.727272727em; /* ~8px */
                }
            }

            h2 + div.card {
                margin-top: 16px;
            }

            @media screen and (max-width: $mobile-break) {
                 h2 + div.card {
                    margin-top: 2em;
                }
            }


        }
    }


