.featured-carousel
{
    .card
    {
        color: #fefefe;
        background-color: #fff;
        position: relative;
        width: 100%;
        height: 567px;
        z-index: 1;
        display: -webkit-box;
        display: -moz-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-transition-property: -webkit-transform;
        -moz-transition-property: -moz-transform;
        -o-transition-property: -o-transform;
        -ms-transition-property: -ms-transform;
        transition-property: transform;
        -webkit-box-sizing: content-box;
        -moz-box-sizing: content-box;
        box-sizing: content-box;

        @media screen and (max-width: $mobile-break)
        {
            height: 680px;
        }

        figure
        {
            position: absolute;
            top: 0;
            bottom: 0;
            width: 100%;
            z-index: 0;

            img{
                width: 100%;
                height: auto;
            }
        }

        &:before
        {
            content: '';
            position: absolute;
            top: 0;
            bottom: 0;
            width: 100%;
            background-color: rgba(35, 31, 32, 0.25);
            z-index: 1;
        }

        .content
        {
            margin: 0 auto 0 19%; /* 342/1800 */
            padding: 0;
            position: absolute;
            bottom: 34px;
            left: 0;
            right: 0;
            z-index: 20;

            @media screen and (max-width: 1260px)
            {
                margin-left: 12%;
            }

            @media screen and (max-width: $mobile-break)
            {
                margin: 0 15px;
            }

            h2
            {
                font-size: ((48px / $font-size-base) + $font-unit);
                max-width: 40%; /* ~470/1200 */
                margin-bottom: 25px;

                @media screen and (max-width: 1580px)
                {
                    max-width: 60%;
                }

                @media screen and (max-width: 1260px)
                {
                    max-width: 80%;
                }

                @media screen and (max-width: $mobile-break)
                {
                    max-width: 90%;
                }
            }

            time
            {
                display: inline-block;
                margin-left: 14px;
                color: #fefefe;
                font-size:  ((11px / $font-size-base) + $font-unit);
            }

            .author
            {
                background-color: #ffff00;
                color: #1a1818;
            }

            p
            {
                font-size:  ((16px / $font-size-base) + $font-unit);
                font-weight: 900;
                margin: 0;
                max-width: 51%; /* ~619/1200 */

                @media screen and (max-width: 1260px)
                {
                    max-width: 80%;
                }

                @media screen and (max-width: $mobile-break)
                {
                    max-width: 90%;
                }
            }

            .share
            {
                position: absolute;
                bottom: 0;
                left: 54%; /* just right of paragraphs */

                @media screen and (max-width: 1260px)
                {
                    left: 83%;
                }

                @media screen and (max-width: $mobile-break)
                {
                    position: static;
                    margin-top: 1em;
                }
            }
        }
    }

    .container
    {
        float: left;
        padding: 0;
        width: 23%;

        @media screen and (max-width: $logo-break)
        {
            width: 35%;
        }

        @media screen and (max-width: $mobile-break)
        {
            float: none;
            width: 100%;
        }
    }

    .top-stories
    {
        margin: 0 11.165048544% 0 7.766990291%; /* (r): 46/412; (l): 32/412 */
        padding: 0;

        @media screen and (max-width: $mobile-break)
        {
            margin: 0 15px;
        }

        h2
        {
            margin: 0;
            padding: 0;
            width: auto;
            font-size: 1.384615385em; /* 18/13 */

            @media screen and (max-width: $mobile-break)
            {
                font-size: 2em; /* 26/13 */
            }
        }

        > h2:first-child
        {
            margin-top: 1.3em;
            padding: 0;
            width: auto;
        }

        article
        {
            position: relative;
            display: block;
            margin: 26px 0 0;
            // padding: 0 0 26px 94px;
            border-bottom: 2px solid #d1d2d4;
            min-height: 108px;
            overflow: hidden;

            // @media screen and (max-width: $mmd-break)
            // {
            //   width: 100%;
            //   padding-left: 94px;
            //   padding-right: 0;
            //   float: none;
            //   height: auto;
            //   border-bottom: 2px solid #d1d2d4;
            // }

            figure
            {
                position: absolute;
                top: 0;
                left: 0;
                margin: 0;
                padding: 0;
                width: 80px;
                height: 80px;
                overflow: hidden;
            }

            h3
            {
                margin: 0;
                font-size: 1.307692308em; /* 17/13 */
                font-weight: bold;
                line-height: 1.411764706em; /* 24/17 */
            }

            time
                + h3
            {
                margin-top: 0.5em; /* ~8px */
            }

            h3
                + time
            {
                margin-top: 0.727272727em; /* ~8px */
            }
        }

        h2
            + div.card
        {
            margin-top: 16px;
        }

        @media screen and (max-width: $mobile-break)
        {
            h2
                + div.card
            {
                margin-top: 2em;
            }
        }
    }
}

